let uid;
let that;
import mine from '@/utils/mine.js';

export default {
	data() {
		return {
			identity: "", //身份码，
			user: "",
			inputCan: (uni.getStorageSync("identity") == 1) ? false : true,
			params: {
				show_float: '',
				USER: '',
				UKEY: '',
				number: '',
				wechat_info: '',
				auto_print:'',//自动打印
			},
		}
	},
	onLoad() {
		that = this;
		uid = wx.getStorageSync("uid")

		that.identity = uni.getStorageSync("identity");
		mine.query_setting().then(res => {
			that.params = res[0]
			if (res[0].wx_openid) {
				that.params.wechat_info = true
			} else {
				that.params.wechat_info = false
			}
		})
	},
	methods: {
		modify_setting() {
			mine.modify_setting(that.params)
		},
		
		//修改是否自动打印
		auto_print(e){
			that.params.auto_print = e.detail.value
			mine.modify_setting(that.params)
		},

		link_wechatinfo(e) {
			let value = e.detail.value

			if (value) {

				if (uni.getStorageSync("openid")) {
					uni.showModal({
						title: '提示',
						content: '请关注服务号"库存表"进行关联',
						confirmText: "关闭",
						success: function(res) {
							if (res.confirm) {} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				} else {
					uni.showModal({
						title: '提示',
						content: '请关注服务号"库存表"进行关联',
						confirmText: "关闭",
						success: function(res) {
							if (res.confirm) {} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
































			} else {
				that.params.wx_openid = ''
				mine.modify_setting(that.params)
			}
		}
	}
}
